		async saveHalfMaterialReg() {
			if(this.loading_yn=='Y'){
				alert('저장중입니다.잠시만 기다려주세요.');
				return;
			}
			this.loading_yn='Y';
			if( this.clm_m_material_type == '' ){
				alert('반제품 유형이 빈칸입니다.');
				this.loading_yn='N';
				return;
			}
			if( this.clm_m_s_material_width == '' ){
				alert('반제품 길이가 빈칸입니다.');
				this.loading_yn='N';
				return;
			}
			if(Number( this.clm_m_s_material_width )<=0){
				alert('반제품 길이가 0또는 음수가 될 수 없습니다.');
				this.loading_yn='N';
				return;
			}
			if( this.clm_m_s_material_height == '' ){
				alert('반제품 넓이가 빈칸입니다.');
				this.loading_yn='N';
				return;
			}
			if(Number( this.clm_m_s_material_height )<=0){
				alert('반제품 넓이가 0또는 음수가 될 수 없습니다.');
				this.loading_yn='N';
				return;
			}
			if( this.clm_m_s_material_thickness == '' ){
				alert('반제품 두께가 빈칸입니다.');
				this.loading_yn='N';
				return;
			}
			if(Number( this.clm_m_s_material_thickness )<=0){
				alert('반제품 두께가 0또는 음수가 될 수 없습니다.');
				this.loading_yn='N';
				return;
			}
			if(Number( this.clm_m_s_material_holl_size )<0){
				alert('반제품 홀 크기가 음수가 될 수 없습니다.');
				this.loading_yn='N';
				return;
			}
			if(this.clm_m_s_material_holl_size==''){
				this.clm_m_s_material_holl_size=0;
			}
			var comma='';
			var comma2='';
			var name ='';
			var code ='';
			var half_product_option ='';
			var half_product_name ='';		
			for(var j=0;j<6;j++){
				code='';
				name='';
				if(half_product_name!=''){
					comma=',';
				}
				if(half_product_option!=''){
					comma2=',';
				}
				if(this.clm_m2_half_fit_yn=='Y' && j==0){
					code='0001';
					name='반조립';
				}
				if(this.clm_m2_gui_yn=='Y'  && j==1){
					code='0002';
					name='귀날림';
				}
				if(this.clm_m2_hom_yn=='Y' && j==2){
					code='0003';
					name='홈가공';
				}
				if(this.clm_m2_bolt_yn=='Y' && j==3){
					code='0004';
					name='볼트작업';
				}
				if(this.clm_m2_holl_yn=='Y' && j==4){
					code='0005';
					name='홀가공';
				}
				if(this.clm_m2_block_fit_yn=='Y' && j==5){
					code='0006';
					name='블록조립';
				}	
				if(name!=''){
					half_product_name= half_product_name+comma+name;
				}
				if(code!=''){
					half_product_option = half_product_option+comma2+code;
				}				
			}
			if(half_product_name.charAt(0)==','){
				half_product_name.substr(1);
			}
			if(half_product_option.charAt(0)==','){
				half_product_option.substr(1);
			}
			this.half_fix_yn='Y';
			
			await axios.post('/back/SCR004005/SubHalfReg', null, { params: {
				clm_m_material_type: this.clm_m_material_type,
				clm_m_s_material_width: this.clm_m_s_material_width,
				clm_m_s_material_height: this.clm_m_s_material_height,
				clm_m_s_material_thickness: this.clm_m_s_material_thickness,
				clm_wood_id : this.clm_m_s_wood_id,
				half_product_name :half_product_name,
				half_product_option:half_product_option,
				clm_user: this.$session.get('user_id')
			}})
			.then(response => {
				if (response.status == '200') {
					this.loading_yn='N';
					if( Number(this.clm_m_s_material_holl_size)!=0 &&  this.clm_m_s_material_holl_size!=''&& typeof this.clm_m_s_material_holl_size!='undefiend'){
