					for(var x=0;x<items.length;x++ ) {
						if(x !=0 && items[x-1].clm_request_delivery_datetime == items[x].clm_request_delivery_datetime) { 
							// 이전 행의 데이터값과 기존 행의 데이터값이 같을 때 rowspan 값은 1이 되어야 함 (그래야 추가 병합이 일어나지 않음)
							items[x].rowspan = 'y'
						}
						if(x !=0 && items[x-1].clm_client_name == items[x].clm_client_name&& items[x-1].clm_request_delivery_datetime == items[x].clm_request_delivery_datetime) { 
							items[x].clientspan = 'y'
						}
						
					}
					this.m_payment_items = items
					console.log(items)
				}
			)
		},
		//자재 멀티 저장
		SaveCutMaterial(){
			var cnt =this.CutItems.length;
			var count =0;
			var Error_YN='N';
			var Error_Count_YN='N';
			var Error_Count2_YN='N';
			var Error_Count3_YN='N';
			if(this.CutItems.length==0){
				Error_YN='Y';
			}
			for(var x=0;x<this.CutItems.length;x++){
				if(Number(this.CutItems[x].clm_order_count)<=0){
					Error_Count_YN='Y';
				}else if(Number(this.CutItems[x].clm_order_count)% 1!= 0 ){
					Error_Count2_YN='Y';
				}else if(Number(this.CutItems[x].clm_order_count) >Number(this.CutItems[x].clm_full_count) ){
					Error_Count3_YN='Y';
				}
			}
			if(Error_YN=='Y'){
				alert('자재를 추가해주시기 바랍니다.');
				this.loading_yn='N';
				return;
			}else if(Error_Count_YN=='Y'){
				alert('수량이 0또는 음수가 될 수 없습니다.');
				this.loading_yn='N';
				return;
			}else if(Error_Count2_YN=='Y'){
				alert('수량이 소수가 될 수 없습니다.');
				this.loading_yn='N';
				return;
			}else if(Error_Count3_YN=='Y'){
				alert('재단 수량이 자재수량을 초과했습니다.');
				this.loading_yn='N';
				return;
			}
			if(this.CutItems.length>1){
				this.clm_m3_lotno = this.CutItems[0].lotno+' 외 '+cnt +'개';
				this.clm_m3_material_name = this.CutItems[0].clm_material_name;
				for(var x=0; x<this.CutItems.length;x++){
					count=count+Number(this.CutItems[x].clm_order_count);
				}
				this.clm_m3_material_count= Number(count);
				this.clm_m3_material_cut_count = Number(count);
			}else{
				this.clm_m3_lotno = this.CutItems[0].lotno;
				this.clm_m3_material_name = this.CutItems[0].clm_material_name;
				this.clm_m3_material_count=  Number(String(this.CutItems[0].clm_order_count).replace(/,/gi, ""));
				this.clm_m3_material_cut_count = Number(String(this.CutItems[0].clm_order_count).replace(/,/gi, ""));
			}
			this.clm_m3_holl_size=this.CutItems[0].clm_holl_size;
			this.clm_m3_material_location_id=this.CutItems[0].clm_material_location_id;
			this.clm_m3_material_location_position_id=this.CutItems[0].clm_material_location_position_id;
			this.CutMultiModal=false;
		},
