		RegHalf(){
			this.loading_yn='N';
			var err_count ='N';
			var err_count_minus ='N';
			var err_lot ='N';
			var err_option ='N';
			for(var x=0;x<this.HalfItems.length;x++){
				if(this.HalfItems[x].lotno==''){
					err_lot='Y';
				}
				if(this.clm_m2_half_fit_yn!='Y' && this.clm_m2_gui_yn!='Y' && this.clm_m2_hom_yn!='Y' && this.clm_m2_bolt_yn!='Y' &&this.clm_m2_holl_yn!='Y' && this.clm_m2_block_fit_yn!='Y' ){
					err_option='Y';
				}
			}
			for(var x=0;x<this.MultiHalfItems.length;x++){
				if(Number(this.MultiHalfItems[x].clm_order_count)<=0 || Number(this.MultiHalfItems[x].clm_order_count)% 1!= 0){
					err_count_minus='Y';
				}
				if(Number(this.MultiHalfItems[x].clm_order_count)>Number(this.MultiHalfItems[x].clm_full_count)){
					err_count='Y';
				}
			}
			if(err_lot=='Y'){
				alert('자재를 모두 선택해 주시기 바랍니다.');
				this.loading_yn='N';
				return;
			}else if(err_count_minus=='Y'){
				alert('수량이 정상적이지 않습니다.');
				this.loading_yn='N';
				return;
			}else if(err_count=='Y'){
				alert('수량이 자재가용수량을 초과하였습니다.');
				this.loading_yn='N';
				return;
			}else if(err_option=='Y'){
				alert('반제품 옵션을 선택해 주시기 바랍니다.');
				this.loading_yn='N';
				return;
			}

			if(this.clm_m2_block_fit_yn=='Y'){
				var sum =0;
				var error_1='N';
				var error_2='N';
				var error_3='N';
				var error_4='N';
				var error_1='N';
				for(var x=0; x<this.BlockHalfItems.length;x++){
					if(Number(this.BlockHalfItems[x].clm_full_count)<Number(this.BlockHalfItems[x].clm_order_count) ){
						error_1='Y';
					}else if(Number(this.BlockHalfItems[x].clm_order_count)<1){
						error_2='Y';
					}else if(Number(this.BlockHalfItems[x].clm_order_count)%1 !=0){
						error_3='Y';
					}else if(this.BlockHalfItems[x].clm_order_count=='' || typeof this.BlockHalfItems[x].clm_order_count=='undefined'){
						error_4='Y';
					}	
					sum=sum+Number(String(this.BlockHalfItems[x].clm_order_count).replace(/,/gi, ""));
				}
				if(error_1=='Y'){
					alert('블록 자재 사용 수량이 가용수량을 초과했습니다.');
					this.loading_yn='N';
					return;
