		OpenMaterialAfter(){
			var Error_income_YN='N';
			if(this.selected_items.length == 0){
				alert('선택된 자재주문이 없습니다.');
			}else if(this.selected_items.length>1000){
				Swal.fire({
					title:'선택한 자재가 1000개를 넘을 수 없습니다.',
					icon: 'error'
				});
			}else{
				var same_yn = 'Y';
				this.loading_yn='N';
				var Error_income_YN='N';
				if(this.selected_items.length == 0){
					alert('선택된 자재주문이 없습니다.');
				}else if(this.selected_items.length>1000){
					Swal.fire({
					title:'선택한 자재가 1000개를 넘을 수 없습니다.',
					icon: 'error'
				});
				}else{
					for(var i=0;i<this.selected_items.length;i++){
						if(this.selected_items[i].clm_income_gubun!='입고완료'){
								Error_income_YN = 'Y';
						}
						// for(var y=0; y<this.selected_items.length;y++){
						// 	if(this.selected_items[i].clm_material_id!=this.selected_items[y].clm_material_id){
						// 		same_yn = 'N';
						// 	}
						// }
						for(var j=0;j<this.selected_items.length;j++){
							// if(this.selected_items[i].clm_material_id != this.selected_items[j].clm_material_id){
							// 	same_yn='N';
							// }
							if(Number(this.selected_items[i].clm_material_width) != Number(this.selected_items[j].clm_material_width)
							|| Number(this.selected_items[i].clm_material_height) != Number(this.selected_items[j].clm_material_height)
							|| Number(this.selected_items[i].clm_material_thickness) != Number(this.selected_items[j].clm_material_thickness)){
								same_yn='N';
							}	
						}
					}
					if(Error_income_YN=='Y'){
						alert('입고되지 않은 자재가 포함되어 있습니다.');
						return;
					}else if(same_yn=='N'){
						alert('규격이 다른 자재가 선택되었습니다.');
						this.loading_yn='N';
						return;
					}
					
					this.tmp_value='after';
					this.AfterCountItems=[{}];
					this.AfterCountItems.splice(0,Object.keys(this.AfterCountItems).length);
					for(var i=0;i<this.selected_items.length;i++){
						this.AfterCountItems.push({
							clm_after_joborder_id:'',
							clm_seq:'',
							clm_material_width:Number(this.selected_items[i].clm_material_width),
							clm_material_height:Number(this.selected_items[i].clm_material_height),
							clm_material_thickness:Number(this.selected_items[i].clm_material_thickness),
							lotno :this.selected_items[i].lotno,
							clm_material_order_id :this.selected_items[i].clm_material_order_id,
							clm_order_income_seq : this.selected_items[i].clm_order_income_seq,
							clm_income_material_seq : this.selected_items[i].clm_income_material_seq,
							clm_material_leftover_seq : this.selected_items[i].clm_material_leftover_seq,
							clm_material_id : this.selected_items[i].clm_material_id,
							clm_order_count :String(this.selected_items[i].clm_cube_size).replace(/,/gi, ""),
							clm_material_name:this.selected_items[i].clm_material_name +'/'+String(this.selected_items[i].clm_cube_size).replace(/,/gi, "")
						});
					}
					
					this.AfterCountModal=true;
					this.SelectModal=false;
				}
			}
