		SaveAfterCount(){
			if(this.loading_yn=='Y'){
				alert('저장중입니다.잠시만 기다려주세요.');
				return;
			}
			this.loading_yn='Y';
			var same_spec = 'N';
			for(var x=0;x<this.AfterCountItems.length;x++){
				if(Number(this.AfterCountItems[x].clm_order_count)<=0 || this.AfterCountItems[x].clm_order_count==''){
					alert('후공정 수량이 0또는 음수입니다.');
					this.loading_yn='N';
					return;
				}else if(Number(this.AfterCountItems[x].clm_order_count)% 1!= 0){
					alert('후공정 수량이 소수입니다.');
