		RegCut() {
			if(this.selected_items.length == 0){
				Swal.fire({
					title:'시작일보다 종료일이 커야합니다.',
					icon: 'error'
				});
				return;
			}
			this.tmp_value='cut';
			this.max_cut_page=1;
			this.current_cut_page=1;
			this.m_width_yn='Y';
			this.m_height_yn='N';
			this.m_thickness_yn='N';
			this.clm_m_cut_joborder_id='';
			var error_yn= 'N';
			var error_material_id_yn= 'N';
			this.clm_m2_block_fit_yn='N';
			var error_holl_yn= 'N';
			this.clm_m3_WHT='W';
			for(var x=0; x<this.selected_items.length;x++){
				if(String(this.selected_items[x].clm_material_order_id).substring(0,2) == 'MP'){
					error_yn='Y';
				}
				// if(x!=0 && this.selected_items[x-1].clm_material_id!=this.selected_items[x].clm_material_id){
				// 	error_material_id_yn='Y';
				// }
				
				for(var y=0; y<this.selected_items.length;y++){
					if(Number(this.selected_items[x].clm_material_width) != Number(this.selected_items[y].clm_material_width)
						&& Number(this.selected_items[x].clm_material_height) != Number(this.selected_items[y].clm_material_height)
						&& Number(this.selected_items[x].clm_material_thickness)!= Number(this.selected_items[y].clm_material_thickness)){
							error_material_id_yn='Y';
					}
				}
				if(x!=0 && this.selected_items[x-1].clm_holl_size!=this.selected_items[x].clm_holl_size){
					error_holl_yn='Y';
				}
			}
			if(error_yn =='Y'){
				alert('반제품은 재단 작업이 불가능합니다.');
				this.loading_yn='N';
				return;
			}else if(error_material_id_yn =='Y'){
				alert('규격이 동일한 자재만 다중선택 할 수 있습니다.');
				this.loading_yn='N';
				return;
			}else if(error_holl_yn =='Y'){
				alert('선택한 자재중 홀크기가 다른 자재가 있습니다.');
				this.loading_yn='N';
				return;
			}
			this.SelectModal=false;
			// var use_gubun =  document.getElementsByName("use_gubun");
