				if(i==1){
					obj[i].checked = true;
				}else{
					obj[i].checked = false;
				}
			}
			this.clm_m4_material_cut_count=0;
			this.clm_m5_material_cut_count=0;
			this.clm_m_s2_material_order='';
			this.clm_m_s_material_order='';
