			this.LossItems3 = [{}],
			this.LossItems3.splice(0,Object.keys(this.LossItems3).length);
			this.loading_yn='N';
			this.submit_yn='N';
			this.clm_m3_radio_gubun='loss';
			this.clm_m4_radio_gubun='loss';
			this.clm_m5_radio_gubun='loss';
			var total_count = 0;
			
			var auto_read_yn='Y';
			if(this.CutItems.length!=0){
				auto_read_yn='N';
			}
			for(var x=0;x<this.selected_items.length;x++){
