		getCutLogModal(){
			
			var value ='';
			if(this.clm_m3_material_type!='0006' && this.clm_m3_material_type!='0007'){
				value='0002';
			}else{
				value='0001';
			}

			let items = axios.get('/back/SCR003008/GetCutLogList',{
				params: {
					clm_wht:this.clm_m3_WHT,
					clm_code : value,
					search: this.clm_m_s_cut_log,
					clm_width:this.clm_m_material_width,
					clm_height:this.clm_m_material_height,
					clm_thickness:this.clm_m_material_thickness,
					}
				}).then(
				response => {		
					console.log(response.data)	
					items = response.data
					setTimeout(() => {
						if(response.data.length==0){
							this.cutLogModal=false;
							alert('규격에 맞는 재단이 없습니다.');
							if(this.clm_m3_cut_joborder_id==''){
								this.cutModal=true;
							}else{
								this.updateModal=true;
							}
						}else{
							if(this.cutLogModal==false){
								this.cutLogModal=true;
							}
						}
						this.m11_document = items
					}, 500)
				},
			)
