		chagneCutOrderCount(index){
			if( Number(this.CutItems[index].clm_order_count)<=0){
				alert('자재 사용수량이 0또는 음수가 될 수 없습니다.');
				this.CutItems[index].clm_order_count=this.CutItems[index].clm_full_count;
			}else if( Number(this.CutItems[index].clm_order_count)> Number(this.CutItems[index].clm_full_count)){
				alert('자재 사용수량이 사용가능수량을 초과했습니다.');
				this.CutItems[index].clm_order_count=this.CutItems[index].clm_full_count;
			}
			var sum= 0;
			for(var x=0; x<this.CutItems.length;x++){
				sum= sum + Number(this.CutItems[x].clm_order_count);
			}
			if( sum - Number(this.clm_m4_material_cut_count)- Number(this.clm_m5_material_cut_count)<=0){
				this.clm_m3_material_cut_count = sum;
				this.clm_m4_material_cut_count=0;
				this.clm_m5_material_cut_count=0;
			}else{
				this.clm_m3_material_cut_count = sum - Number(this.clm_m4_material_cut_count)- Number(this.clm_m5_material_cut_count)
			}
			this.clm_m3_material_count = sum;
		},
