			for(var x=0; x<this.CutItems.length;x++){
				count= count + Number(this.CutItems[x].clm_order_count);
			}
			this.clm_m3_material_count =count;
			this.clm_m3_material_cut_count=count;
			this.clm_m4_material_cut_count=0;
			this.clm_m5_material_cut_count=0;
