			var sub_width=0;
			var cnt =0;
			var index = 'L';
			sub_width = Number(this.clm_m3_s1_material_width)*Number(this.clm_m3_s1_material_count)+Number(this.clm_m3_s2_material_width)*Number(this.clm_m3_s2_material_count)+Number(this.clm_m3_s3_material_width)*Number(this.clm_m3_s3_material_count)+Number(this.clm_m3_s4_material_width)*Number(this.clm_m3_s4_material_count)+Number(this.clm_m3_s5_material_width)*Number(this.clm_m3_s5_material_count)+Number(this.clm_m3_s6_material_width)*Number(this.clm_m3_s6_material_count)+Number(this.clm_m3_s7_material_width)*Number(this.clm_m3_s7_material_count)+Number(this.clm_m3_s8_material_width)*Number(this.clm_m3_s8_material_count);
